/**
 * @hidden
 */
export class DOMRectPolyfill {
  x = 0;
  y = 0;
  width = 0;
  height = 0;

  static fromRect(
    rect: { x?: number; y?: number; width?: number; height?: number } = {},
  ) {
    return new this(
      rect.x ?? 0,
      rect.y ?? 0,
      rect.width ?? 0,
      rect.height ?? 0,
    );
  }

  constructor(x?: number, y?: number, width?: number, height?: number) {
    if (x != null) {
      this.x = x;
    }
    if (y != null) {
      this.y = y;
    }
    if (width != null) {
      this.width = width;
    }
    if (height != null) {
      this.height = height;
    }
  }

  get top() {
    return this.y;
  }

  get right() {
    return this.x + this.width;
  }

  get bottom() {
    return this.y + this.height;
  }

  get left() {
    return this.x;
  }

  toJSON() {
    return {
      x: this.x,
      y: this.y,
      width: this.width,
      height: this.height,
      top: this.top,
      right: this.right,
      bottom: this.bottom,
      left: this.left,
    };
  }
}
